.listheader {
	padding: .5em 1% .2em 0;
	overflow: hidden;
	position: relative;
	display: block;
	line-height: 1.6;
	font-size: 16px;
}

.listheader img {
	height: .8em;
	vertical-align: middle;
	margin-left: .2em;
}

.listheader span {
	display: inline-block;
	vertical-align: middle;
	font-size: 16px;
}

.listheader:before {
	display: inline-block;
	width: 2px;
	background-color: red;
	height: 1em;
	content: "";
	margin-right: .3em;
	border-radius: 10px;
	vertical-align: middle
}

#playall .flexlist {
	padding: .5em 0 .5em
}

#playall img {
	height: 1.5em;
}

#pa-count text {
	color: #888;
	font-size: 24px
}

.menu .mn_list {
	display: flex;
	flex-flow: row;
	align-items: stretch;
	width: 100%;
	justify-content: space-around
}

.menu .mn_list:last-child .cmain,
.menu .mn_list:last-child .rdes {
	border: none;
}

.menu .cmain {
	flex: 1;
	border-bottom: 1px solid #e1e2e3;
	display: flex;
	align-items: center;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	padding-right: 15px
}

.menu .mn_ico {
	text-align: center;
	padding: .5em
}

.menu .mn_ico img {
	max-height: 2.2em;
}

.menu .arrow {
	display: inline-block;
	margin: 0 .5em;
	vertical-align: middle;
}

.menu img {
	vertical-align: top;
}

.menu .rdes {
	text-align: right;
	border-bottom: 1px solid #e1e2e3;
	color: #999;
	display: flex;
	align-items: center;
	justify-content: flex-end
}

#headerblur {
	position: fixed;
	z-index: 5;
	height: 40px;
	top: 0;
	width: 100%;
	overflow: hidden;
	background: #fff;
	max-width: 750px;
}

#plh-cover {
	margin: 0 3% .5em;
	position: relative;
	flex: 0 0 35%;
	height: 0;
	padding-top: 35%;
}

#plh-cover .music_cover {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
}

#headerblur .blurbg {
	height: 0;
	padding-top: 100%
}

#plist-header {
	position: relative;
	overflow: hidden;
	padding-top: 40px;
}

#plh-main {
	position: relative;
	z-index: 3;
	display: flex;
	align-items: center
}

#plh-playinfo {
	position: absolute;
	z-index: 3;
	right: 5px;
	bottom: 5px
}

#plh-playinfo img {
	height: 1.5em;
	vertical-align: top;
	background-color: rgba(0, 0, 0, .2);
	border-radius: 50%;
}

#user_ava {
	width: 2em;
	height: 2em;
	border-radius: 50%;
	margin-right: .5em;
	vertical-align: middle;
}

#plh-cnt {
	color: #fff;
	flex: 0 0 58%;
	font-size: .8em;
	line-height: 1.8
}

#plh-cnt a {
	color: #fff;
}

#music_h_name {
	padding: 0 0 1em;
	display: block;
	font-size: 1.25em;
}

#plh_action {
	display: flex;
	position: relative;
	z-index: 3;
	clear: both;
	width: 100%;
	line-height: 2;
	text-align: center;
	color: #fff;
}

#plh_action a {
	color: #fff
}

#plh_action div {
	border-bottom: none;
	flex: 1;
}

#plh_action div img {
	width: 4em;
	display: block;
	margin: -.5em auto -1em
}

#plh_action span {
	display: block;
	line-height: 2
}

#artheader {
	background-image: linear-gradient(rgba(0, 0, 0, .2), rgba(0, 0, 0, 0));
	background-color: transparent;
}

#headerblur {
	background: none;
}

#art_cover {
	transform: scale(1.25)
}

#artist_header {
	position: relative;
	height: 0;
	overflow: hidden;
	z-index: 2;
	padding-top: 62%;
	width: 100%;
	left: 0;
	top: 0;
	max-width: 750px;
	margin-top: -40px;
}

#artist_header>img {
	width: 100%;
	position: absolute;
	left: 0;
	top: 0;
}

#ahw_wrap {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	padding: 5em 1em 1em;
	box-sizing: border-box;
	text-align: right;
	background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(0, 0, 0, .6))
}

.ahw_btn {
	display: inline-block;
	padding: 0 1.2em 0 1em;
	border: 1px solid #fff;
	color: #fff;
	border-radius: 3em;
	line-height: 2;
	font-size: .8em;
	margin-top: .5em;
	vertical-align: middle;
}

.ahw_btn img {
	height: 2em;
	vertical-align: top;
}

.ahw_name {
	float: left;
	color: #fff;
	text-align: left;
	line-height: 1.6;
}

.ahw_name p {
	color: #aaa;
	font-size: .8em;
}

.hqico {
	background: url(../../static/images/cm2_list_sup_hot@2x.png) no-repeat scroll top left;
}

.cover_ico {
	position: absolute;
	z-index: 2;
	left: 0;
	top: 0;
	width: 2.5em;
	height: 2.5em;
	-webkit-background-size: 100% auto;
	background-size: 100% auto;
}

#catewrap {
	padding: 0 1%;
	box-sizing: border-box;
	position: fixed;
	width: 100%;
	height: 100%;
	z-index: 100;
	left: 0;
	top: 0;
	background-color: #fff;
	overflow: auto;
}

#closecatelist {
	margin: .5em auto;
}

.catelist {
	margin-bottom: .5em;
	border: 1px solid #eee;
	border-width: 1px 0 0 1px;
}

.catelist:after {
	display: table;
	clear: both;
	content: '';
}

.cl_list {
	text-align: center;
	position: relative;
	z-index: 1;
	width: 25%;
	float: left;
	line-height: 1.5em;
	height: 3.5em;
	padding: 1em 0;
	box-sizing: border-box;
	cursor: pointer;
}

.cl_list:after {
	content: '';
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	border: 1px solid #eee;
	event-pointer: none;
	border-width: 0 1px 1px 0;
	display: block;
}

.cl_ico {
	height: 7em;
	padding: 0;
	overflow: hidden;
	padding: 0;
}

.cl_ico span {
	display: block;
	width: 3em;
	position: absolute;
	top: 50%;
	transform: translate(-50%, -50%);
	left: 50%;
	line-height: 2;
}

.cl_ico img {
	display: block;
}

#cateall {
	float: none;
	width: auto;
	display: block;
	margin: 0 3px 1em;
}

.cl_ico_checked,
.cl_ico_hot {
	content: "";
	position: absolute;
}

.cl_ico_hot {
	background-image: url(data:image/png;base64,);
	width: 2em;
	height: 2em;
	left: 0;
	top: 0;
	background-size: 100% auto;
}

.cl_ico_checked {
	background-image: url(../../static/images/cm2_discover_slted@2x.png);
	width: 1.5em;
	height: 1.5em;
	right: 0;
	bottom: 0;
	background-size: 100% 100%;
}

.checked {
	z-index: 2
}

.checked:after {
	border-color: #BB2C08;
	border-width: 1px;
	left: -1px;
	top: -1px;
	display: block;
}

.close {
	width: 3em;
	height: 3em;
	background-size: 100% auto;
	background-position: center center;
	background-repeat: no-repeat;
	background-image: url(data:image/png;base64,)
}

.arrow {
	width: 16px;
	height: 18px;
	background-size: 100% auto;
	background-position: center center;
	background-repeat: no-repeat;
	background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAqCAMAAACeG2tsAAAAJFBMVEUAAACysrKysrKysrKysrKysrKysrKysrKysrKysrKysrKysrIIg5e5AAAAC3RSTlMAV6UD/KinqaaqWKI0gd4AAABFSURBVDjLY2AgAzAyMWMXZ+FmxSrDxs2NXYaZfZDLsI8YGQ5sEqxACTYc4pzMtBVnH2Hi2BIhE67EyYUrqzFw4cicpAIAj9UHHcF4JeQAAAAASUVORK5CYII=)
}

.arrow-white {
	width: 12px;
	height: 1em;
	background-image: url(data:image/png;base64,)
}

.mv_cover {
	background-size: 100% auto;
	background-position: center center;
	background-repeat: no-repeat;
	background-image: url("data:image/png;base64,")
}

.user_avator {
	background-size: cover;
	border-radius: 50%;
	background-image: url(data:image/png;base64,)
}

.music_cover {
	background-size: cover;
	background-image: url(data:image/png;base64,);
}

.album_cover {
	background-size: cover;
	background-image: url(data:image/png;base64,);
}


